import './App.css';
import { HomeLayout } from './layouts/HomeLayout';
import CCDNavbar from './components/CCDNavbar';
import { createContext, useEffect, useMemo, useState } from 'react';

export const HomeContext = createContext({
  isDesktop: false,
  videoReady: false,
  setVideoReady: () => {},
  setIsDesktop: () => {}
});

function App() {
  const [isDesktop, setIsDesktop] = useState(false);
  const [desktopParsed, setDesktopParksed] = useState(false);
  const [videoReady, setVideoReady] = useState(false);

  const homeMemoValues = useMemo(
    () => ({
      isDesktop,
      videoReady,
      setIsDesktop,
      setVideoReady
    }),
    [isDesktop, videoReady]
  );

  useEffect(() => {
    setIsDesktop(window.innerWidth > 992);
    setDesktopParksed(true);
  }, []);

  return (
    <div className="App">
      <HomeContext.Provider value={homeMemoValues}>
        {desktopParsed ? (
          <>
            <header
              id="navbar"
              className="navbar navbar-inverse container-fluid"
              style={
                isDesktop
                  ? {
                      display: 'flex',
                      flexDirection: 'row',
                      justifyContent: 'center'
                    }
                  : null
              }
            >
              {/*{videoReady ? <CCDNavbar /> : null}*/}
              <CCDNavbar />
            </header>
            <HomeLayout />
          </>
        ) : null}
      </HomeContext.Provider>
    </div>
  );
}

export default App;
