import { useEffect, useState } from 'react';
import { fetchFrontAnnouncement } from '../api/APICalls';
import { Modal, ModalBody } from 'react-bootstrap';

export const FrontAnnouncementModal = () => {
  const [blockFetched, setBlockFetched] = useState(false);
  const [blockContent, setBlockContent] = useState('');

  useEffect(() => {
    fetchFrontAnnouncement()
      .then((res) => {
        if (res.status === 200) {
          res.json().then((json) => {
            setBlockContent(json);
          });
        }
      })
      .finally(() => {
        setBlockFetched(true);
      });
  }, []);

  return <>{blockFetched && blockContent ? <AnnouncementModal content={blockContent} /> : null}</>;
};

const AnnouncementModal = (props) => {
  const [showPopupModal, setShowPopupModal] = useState(true);

  return (
    <Modal
      show={showPopupModal}
      onHide={() => {
        setShowPopupModal(false);
      }}
      fullscreen="lg-down"
      centered
    >
      <ModalBody style={{ backgroundColor: 'black' }}>
        <div dangerouslySetInnerHTML={{ __html: props.content }} />
      </ModalBody>
    </Modal>
  );
};
