import { Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { useEffect, useState } from 'react';
import { fetchMainMenu } from '../api/APICalls';

export default function CCDNavbar() {
  const [menuJSON, setMenuJSON] = useState(null);
  const [menuFetched, setMenuFetched] = useState(false);

  useEffect(() => {
    if (!menuFetched) {
      fetchMainMenu().then((res) => {
        if (res.status === 200) {
          res.json().then((json) => {
            setMenuJSON(json);
            setMenuFetched(true);
          });
        }
      });
    }
  }, []);

  return (
    <>
      {menuFetched ? (
        <Navbar
          expand="lg"
          variant="dark"
          bg="dark"
          style={{
            padding: '.5em'
          }}
        >
          {/*<Navbar.Brand*/}
          {/*  href="#home"*/}
          {/*  color="white"*/}
          {/*  style={{ color: 'white', fontSize: '1.5em', fontWeight: 800 }}*/}
          {/*>*/}
          {/*  Chattahoochee Contra Dancers*/}
          {/*</Navbar.Brand>*/}
          <Navbar.Toggle aria-controls="navbar-collapse" id="navbar-toggle" />
          <Navbar.Collapse id="navbar-collapse">
            <Nav className="me-auto menu menu-main nav navbar-nav">
              {menuJSON
                ? menuJSON.map((menuItem, id) => <CCDNavLink key={id} {...menuItem} />)
                : ''}
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      ) : (
        ''
      )}
    </>
  );
}

const CCDNavLink = (props) => {
  const { below } = props;

  return <>{!below ? <CCDNavLinkSingle {...props} /> : <CCDNavLinkDropdown {...props} />}</>;
};

const CCDNavLinkSingle = (props) => {
  const { title, uri, absolute } = props;

  return (
    <Nav.Link href={absolute} data-drupal-link-system-path={uri}>
      {title}
    </Nav.Link>
  );
};

const CCDNavLinkDropdown = (props) => {
  const { title } = props;

  return (
    <NavDropdown title={title} className="expanded dropdown">
      {props.below.map((item, id) => (
        <CCDNavLinkSingle key={id} {...item} />
      ))}
    </NavDropdown>
  );
};
