import { useContext, useEffect, useState } from 'react';
import { Container, Fade, Row } from 'react-bootstrap';
import { HighlightedBlock } from '../components/HighlightedBlock';
import { EventsBlock } from '../components/EventsBlock';
import videojs from 'video.js';
import { Footer } from '../components/Footer';
import { HomeContext } from '../App';
import { FrontAnnouncementModal } from '../components/FrontAnnouncementModal';

export const HomeLayout = () => {
  const [showContainer, setShowContainer] = useState(false);
  const { isDesktop, videoReady } = useContext(HomeContext);

  function handleFinishEventLoad() {
    setShowContainer(true);
  }

  const videoSrc = isDesktop
    ? // eslint-disable-next-line no-undef
      `/sites/default/files/PotentSmall.mp4`
    : // eslint-disable-next-line no-undef
      `/sites/default/files/PotentMobile.mp4`;

  const videoJsOptions = {
    autoplay: true,
    controls: true,
    fullscreen: true,
    options: { navigationUI: 'hide' },
    loop: true,
    muted: true,
    sources: [
      {
        src: videoSrc,
        type: 'video/mp4'
      }
    ]
  };

  useEffect(() => {
    // containerClassCallback(videoReady, fadeInContainer);
  }, [videoReady]);

  return (
    <>
      {isDesktop ? (
        <Fade in={videoReady} transitionClasses={{ entered: 'in', entering: 'in' }}>
          <VideoContainer {...videoJsOptions} />
        </Fade>
      ) : (
        ''
      )}

      <Fade
        in={videoReady && showContainer}
        timeout={2000}
        transitionClasses={{ entered: 'in', entering: 'in' }}
      >
        <Container role="main" className={'main-container'}>
          <FrontAnnouncementModal />
          <Row xs={12}>
            <HighlightedBlock />
            <h3 className="page-header">Welcome to Contra Dancing In Atlanta</h3>
            {!isDesktop ? <VideoContainer {...videoJsOptions} /> : ''}
            <h3 className="block-title">Upcoming Events</h3>
            <EventsBlock onFinishedLoading={handleFinishEventLoad} />
          </Row>
        </Container>
      </Fade>
      {showContainer && videoReady ? <Footer /> : ''}
    </>
  );
};

const VideoContainer = (props) => {
  const [videoNode, setVideoNode] = useState(null);
  const { isDesktop, setVideoReady } = useContext(HomeContext);

  useEffect(() => {
    if (videoNode) {
      const player = videojs(videoNode, props, function onPlayerReady() {
        player.play();
        if (isDesktop) {
          player.enterFullWindow();
        }
      });
      player.on('timeupdate', () => {
        if (player.currentTime() > 0.5) setVideoReady(true);
      });
    }
  }, [videoNode]);

  return (
    <div>
      <div data-vjs-player>
        <video
          id={`${isDesktop ? 'video-background' : 'video-foreground'}`}
          ref={(node) => setVideoNode(node)}
          className={`video-js ${isDesktop ? 'video-background' : 'video-foreground'}`}
        ></video>
      </div>
    </div>
  );
};
