import { useEffect, useState } from 'react';
import { fetchFrontMainBlock1 } from '../api/APICalls';

export const HighlightedBlock = () => {
  const [blockFetched, setBlockFetched] = useState(false);
  const [blockContent, setBlockContent] = useState('');

  useEffect(() => {
    fetchFrontMainBlock1()
      .then((res) => {
        if (res.status === 200) {
          res.json().then((json) => {
            setBlockContent(json);
          });
        }
      })
      .then(() => {
        setBlockFetched(true);
      });
  }, []);

  return (
    <>
      {blockFetched && blockContent ? (
        <div dangerouslySetInnerHTML={{ __html: blockContent }} />
      ) : null}
    </>
  );
};
