import { useContext, useEffect, useState } from 'react';
import { fetchFrontEvents } from '../api/APICalls';
import 'add-to-calendar-button/assets/css/atcb.min.css';
import { atcb_action } from 'add-to-calendar-button';
import { Button, Col, Container, Row } from 'react-bootstrap';
import { HomeContext } from '../App';

export function EventsBlock(props) {
  const [eventsJSON, setEventsJSON] = useState(null);
  const [eventsFetched, setEventsFetched] = useState(false);

  useEffect(() => {
    if (!eventsFetched) {
      fetchFrontEvents().then((res) => {
        if (res.status === 200) {
          res.json().then((json) => {
            setEventsJSON(json);
            setEventsFetched(true);
          });
        }
      });
    } else {
      props.onFinishedLoading();
    }
  }, [eventsJSON, eventsFetched]);

  return (
    <Container>
      {eventsJSON ? eventsJSON.map((event, index) => <EventBlock key={index} {...event} />) : ''}
    </Container>
  );
}

const EventBlock = (props) => {
  const { isDesktop } = useContext(HomeContext);

  const {
    title,
    body,
    field_event_date,
    field_event_date_1,
    view_node_1,
    field_cost,
    field_host,
    field_sound,
    field_band_image_new,
    field_band,
    field_person_image
  } = props;

  return (
    <Row className="event-container my-3 py-3">
      <Col xs={12} lg={6}>
        <Col xs={12}>
          <p className="event-name">
            <a href={view_node_1}>{title}</a>
          </p>
        </Col>
        <Col xs={12}>
          <p>
            <b>{body}</b>
          </p>
        </Col>
        <Col xs={12}>
          <EventDate startRaw={field_event_date} endRaw={field_event_date_1} />
        </Col>
        <Col xs={12}>
          <p>
            Sound: <b>{field_sound}</b>
          </p>
        </Col>
        <Col xs={12}>
          <p>
            Host: <b>{field_host}</b>{' '}
          </p>
        </Col>
        <Col xs={12}>
          <p>Location:</p>
        </Col>
        <Col xs={12}>
          <p> Cost: {field_cost}</p>
        </Col>
        <Col xs={12}>
          <ATCButton {...props} />
        </Col>
      </Col>
      {isDesktop ? (
        <>
          <Col
            xs={12}
            lg={3}
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center'
            }}
          >
            <Col xs={12} style={{ width: '100%' }}>
              <img
                style={{ width: '100%' }}
                src={field_band_image_new}
                alt={`Picture of ${field_band}`}
                title={`Picture of ${field_band}`}
              />
            </Col>
          </Col>
          <Col
            xs={12}
            lg={3}
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center'
            }}
          >
            <Col xs={12}>
              <img
                style={{ width: '100%', maxWidth: '200px', maxHeight: '200px' }}
                src={field_person_image}
                alt={`Picture of ${field_band}`}
                title={`Picture of ${field_band}`}
              />
            </Col>
          </Col>
        </>
      ) : (
        ''
      )}
    </Row>
  );
};

const EventDate = (props) => {
  const [date, setDate] = useState(null);
  const [start, setStart] = useState(null);
  const [end, setEnd] = useState(null);

  const { startRaw, endRaw } = props;

  useEffect(() => {
    const startTemp = new Date(startRaw);
    const endTemp = new Date(endRaw);
    const offset = startTemp.getTimezoneOffset();
    const startFinal = new Date(startTemp.getTime() - offset * 1000 * 60);
    const endFinal = new Date(endTemp.getTime() - offset * 1000 * 60);
    setDate(
      startFinal.toLocaleDateString('en-us', {
        timeZone: 'America/New_York',
        weekday: 'long',
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      })
    );
    setStart(
      startFinal.toLocaleString('en-US', {
        timeZone: 'America/New_York',
        hour: 'numeric',
        minute: '2-digit',
        hour12: true
      })
    );
    setEnd(
      endFinal.toLocaleString('en-US', {
        timeZone: 'America/New_York',
        hour: 'numeric',
        minute: '2-digit',
        hour12: true
      })
    );
  }, [start]);

  return (
    <>
      <p>{date}</p>
      <p>
        {start} - {end}
      </p>
    </>
  );
};

const ATCButton = (props) => {
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  const { field_event_date, field_event_date_1, title, body, field_location } = props;

  useEffect(() => {
    const startTemp = new Date(field_event_date);
    const endTemp = new Date(field_event_date_1);
    const offset = startTemp.getTimezoneOffset();
    const tempStartDate = new Date(startTemp.getTime() - offset * 1000 * 60);
    const tempEndDate = new Date(endTemp.getTime() - offset * 1000 * 60);
    setStartDate(tempStartDate);
    setEndDate(tempEndDate);
  }, []);

  function handleClick(e) {
    e.preventDefault();
    atcb_action({
      label: 'Add to Calendar',
      name: title,
      description: body,
      startDate: startDate.toISOString().split('T')[0],
      endDate: endDate.toISOString().split('T')[0],
      startTime: startDate.toLocaleString('en-US', {
        day: undefined,
        hour: '2-digit',
        minute: '2-digit',
        hour12: false
      }),
      endTime: endDate.toLocaleString('en-US', {
        day: undefined,
        hour: '2-digit',
        minute: '2-digit',
        hour12: false
      }),
      location: field_location,
      options: ['Apple', 'Google', 'iCal', 'Microsoft365', 'Outlook.com', 'Yahoo'],
      timeZone: 'America/New York',
      timeZoneOffset: '-04:00',
      trigger: 'click',
      iCalFileName: 'Reminder-Event'
    });
  }
  return (
    <Button type="button" variant="primary" title="Add to Calendar" onClick={handleClick}>
      Add To Calendar
    </Button>
  );
};
