import fetchWithTimeout from '../utils/FetchWithTimeout';

export const apiCall = (url, method = 'GET', contentType = 'application/json') =>
  fetchWithTimeout(url, {
    timeout: 4000,
    method,
    headers: {
      // eslint-disable-next-line no-undef
      Host: `https://${process.env.REACT_APP_API_URL}`,
      'Access-Control-Allow-Origin': `*`,
      // eslint-disable-next-line no-undef
      Origin: `https://${process.env.REACT_APP_HOSTNAME}`,
      'Content-Type': contentType
    },
    credentials: 'include'
  });
export const fetchMainMenu = () => {
  // eslint-disable-next-line no-undef
  const url = `/api/menu_items/main`;
  return apiCall(url);
};

export const fetchFrontEvents = () => {
  // eslint-disable-next-line no-undef
  const url = `/api/events/front?_format=json`;
  return apiCall(url);
};

export const fetchFrontMainBlock1 = () => {
  const url = `/api/block-expose/block?_format=json&block_id=frontblock1`;
  return apiCall(url);
};

export const fetchFrontAnnouncement = () => {
  const url = `/api/block-expose/block?_format=json&block_id=frontannouncementblock`;
  return apiCall(url);
};
